import React, { useState, useRef } from 'react';
import { Link } from 'gatsby';
import SectionTitle from './section-titles';
import StoreIcons from './../shared/store-icons';
import Popup from './../shared/popup';

import payIcon from '../../images/index/phone-section-hand.svg';
import playIcon from '../../images/index/phone-section-live-tv.svg';
import phoneImage from '../../images/index/iphone-netsurf-play.svg';


const PhoneSection = () => {
  return (
    <>
      <div className='row phone-section'>

        <div className='container'>
          <SectionTitle>
            <h2><span className='text-purple'>Безплатно към всички пакети</span> NetSurf Play</h2>
            <hr className='center' />
          </SectionTitle>

          {/* <div className='phone-section-wrapper'> */}
          {/* <div className=''> */}
          {/* <SVG /> */}
          {/* </div> */}
          <div className='row d-flex'>
            <div className='col-6 col-md-3 order-1 order-md-1 text-center d-flex justify-content-center align-items-center'>
              <HoverIcon
                icon={payIcon}
                left={true}
                alt={'Плати с един клик'}
                title={'Плащай си сметките с един „цък”'}
                content={'С  NetSurf Play е вече фасулска работа да си платиш сметката или да провериш цялата история на плащанията си от самото начало. Удобството е вече тук, в твоя джоб, с всичките ни абонаментни планове за телевизия и интернет. От хубавото и лесното никой не бяга.'}
              />
            </div>
            <div className='col-12 col-md-6 order-0 order-md-2'>
              <img className='' src={phoneImage} alt='Netsurf Play приложение' />
              <StoreIcons />
            </div>
            <div className='col-6 col-md-3 order-2 order-md-3 text-center d-flex justify-content-center align-items-center'>
              <HoverIcon
                icon={playIcon}
                left={false}
                alt={'Гледай телевизия в реално време'}
                title={'Гледай любимата си телевизия в реално време'}
                content={'Пълен достъп и контрол до използваните от теб телевизия и интернет услуги – честно, не ти трябва друго мобилно приложение за стрийминг, освен NetSurf Play. Потребителският интерфейс е лесен и интуитивен и ти дава безброй възможности да гледаш филмите, сериалите и мачовете онлайн и чрез Android, и чрез iOS. Мобилна телевизия навсякъде и по всяко време. Да, свободата е за избраните'}
              />
            </div>
          </div>
          {/* </div> */}

          <div className='text-center'>
            <Link to={'/paketi'} className='btn btn-secondary my-5'>Научи повече</Link>
          </div>
        </div>
      </div>
    </>
  );
}


const HoverIcon = ({ icon, alt, title, content, left }) => {
  const target = useRef(null);
  const [show, setShow] = useState(false);

  return (
    <>
      <div style={{ width: '200px', height: '200px' }} ref={target}>
        <img
          className='img-fluid'
          src={icon}
          alt={alt}
          onClick={(_) => setShow(!show)}
        />
        <p className='mt-2'>{title}</p>
      </div>
      <Popup
        showTelephonePopover={show}
        target={target}
        placement='top'
      >
        <div className={'row d-flex align-items-center ' + (left ? 'justify-content-start' : 'justify-content-end')}>
          <div className='phone-pop-up bg-white col-12 col-md-6 px-5 py-3'>
            <div className=''>
              <h3>{title}</h3>
              <p>{content}</p>
            </div>
          </div>
        </div>
      </Popup>
    </>
  )
}

export default PhoneSection;


