import React from 'react';
import { Link } from 'gatsby';
import Img from "gatsby-image";

const ImageTextSection = ({ imgSrc, imgAlt, sectionTitle, undertext, list, linkTo, linkText, order }) => {

  return (
    <>
      <div className='row d-flex my-5 mx-md-5'>
        {order && (
          <div className='text-center d-block d-lg-none text-center w-100'>
            {sectionTitle}
            <hr className='my-4 d-none d-lg-block' />
            <hr className='my-4 center d-block d-lg-none' />
          </div>
        )}

        <div className={'col-12 col-lg-6 ' + (order && 'order-lg-1')}>
          <Img fluid={imgSrc} alt={imgAlt} />
        </div>
        <div className={'col-12 col-lg-6 px-md-5 text-center text-lg-left ' + (order && 'order-lg-0')}>
          {!order && (
            <div className='text-center d-block d-lg-none'>
              {sectionTitle}
              <hr className='my-4 d-none d-lg-block' />
              <hr className='my-4 center d-block d-lg-none' />
            </div>
          )}
          <div className='text-lg-left d-none d-lg-block'>
            {sectionTitle}
            <hr className='my-4 d-none d-lg-block' />
            <hr className='my-4 d-block d-lg-none' />
          </div>
          {/* {sectionTitle} */}
          {/* <hr className='my-4 d-none d-lg-block' /> */}
          {/* <hr className='my-4 center d-block d-lg-none' /> */}
          <p>{undertext}</p>
          <p className='mb-5'>
            <ul className='blue-ticks'>
              {list.map((item, index) => (
                <li key={index}>{item}</li>
              ))}
            </ul>
          </p>
          <Link to={linkTo} className='btn btn-primary green'>{linkText}</Link>
        </div>
      </div>
    </>
  )
}

export default ImageTextSection;