import React from 'react';
import Slider from 'react-slick';
import SectionTitle from './section-titles';
import styled from 'styled-components';

import testimonialBg from '../../images/index/testimonial-bg.svg';
import arrowRight from '../../images/index/right-arrow.svg'
import arrowLeft from '../../images/index/left-arrow.svg'

import constants from '../../constants/index';

const TestimonialWrapper = styled.div`
  background-image: url(${(props) => props.testimonialBg});
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  min-height: 540px;
  width: 540px;
  text-align: center;
`;

const TestimonialTextBox = styled.div`
  width: 540px;
`
const TestimonialSlider = () => {

  return (
    <>
      <div className='row'>
        <div className='col-12'>
          <SectionTitle>
            <h2>Какво мислят <span className='text-purple'>другите за нас</span></h2>
            <hr className='center' />
          </SectionTitle>
        </div>

        <div className='col-12 px-4 reasons-slider'>
          <Slider {...settings}>
            {constants.clientOpinions.map(({name, opinion, date}, index) => (
              <TestimonialWrapper key={index} testimonialBg={testimonialBg} className='col-12 d-flex justify-content-center align-items-center'>
                <TestimonialTextBox className='px-4 testimonial pt-5'>
                  <h3>{name}</h3>
                  <p className='text-testimonial'>{opinion}</p>
                  <h5 className='d-none d-sm-inline'>{date}</h5>
                </TestimonialTextBox>
              </TestimonialWrapper>
            ))}
          </Slider>
        </div>
      </div>
    </>
  )
}

const NextArrow = ({ className, style, onClick }) => {
  return (
    <div
      className={className}
      style={{ ...style, display: 'block' }}
      onClick={onClick}
    >
      <img src={arrowRight} className='img-fluid' alt="Arrow Right" />
    </div>
  );
}
const PrevArrow = ({ className, style, onClick }) => {
  return (
    <div
      className={className}
      style={{ ...style, display: 'block'}}
      onClick={onClick}
    >
      <img src={arrowLeft} className='img-fluid' alt="Arrow Left" />
    </div>
  );
}

const settings = {
  arrows: true,
  speed: 2000,
  infinite: false,
  centerMode: false,
  autoplay: false,
  autoplaySpeed: 5000,
  slidesToShow: 1,
  slidesToScroll: 1,
  focusOnSelect: true,
  pauseOnFocus: true,
  nextArrow: <NextArrow />,
  prevArrow: <PrevArrow />,
  responsive: [
    {
      breakpoint: 700,
      settings: {
        arrows: false,
        dots: true,
      } 
    },
  ]
}

export default TestimonialSlider;