import React from 'react';


const SectionTitle = (props) => (
  <>
    <div className={'row d-flex justify-content-center align-items-center w-100 mt-5 ' + (props.left ? 'text-left' : 'text-center')} >
      <div className='col-12 col-md-5 col-lg-3 my-5'>
        {props.children}
      </div>
    </div>
    {props.undertext && (
      <div className={'row d-flex justify-content-center align-items-center text-center w-100 mb-5 ' + (props.left ? 'text-left' : 'text-center')}>
        <div className='col-12 col-md-9 col-lg-5'><p>{props.undertext}</p></div>
      </div>
    )}
  </>
)

export default SectionTitle;